*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow-x: hidden;
}

body {
    font-family: 'Raleway', sans-serif;
}

.error,.info {
    text-align: center;
    margin: 0 auto;
    color: #535353;
    opacity: 0.6;
    text-shadow: 0 0 2px #868686;
    width: 100%;
}

.search-wrapper {
    -ms-flex-positive: 1;
        flex-grow: 1;
    max-height: 95px;
    border-radius: 8px;
    background-color: rgba(253, 156, 1, 0.8);
    margin-left: 20px;
    display: -ms-flexbox;
    display: flex;
    padding: 20px;
}

.search-elem {
    background-color: transparent;
    outline: none;
    border: none;
    border-radius: 8px;
}

.button-order {
    margin-left: 60px;
    font-size: 20px;
}

.button-sort {
    font-weight: bold;
    margin-left: 30px;
}

.button-search {
    background-color: transparent;
    padding: 0;
    padding-right: 8px;
}

.input-wrapper {
    background-color: whitesmoke;
    height: auto;
    display: -ms-flexbox;
    display: flex;
}

.input-actors {
    padding: 0 10px;
}

button {
    padding: 0 8px;
    cursor: pointer;
    position: relative;
}

button::after {
    color: whitesmoke;
    content: 'Info';
    position: absolute;
    top: -17px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    font-size: 14px;
    font-weight: bold;
}

.button-sort::after {
    content: 'sort';
}
.button-order::after {
    content: 'order';
}
.button-search::after {
    content: '';
}

@media (max-width: 600px) {
    .search-wrapper {
        width: 100%;
        max-height: 100px;
        padding: 5px;
        margin-left: 0;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }

    .input-wrapper {
        width: 100%;
        margin-bottom: 10px;
    }

    .input-actors {
        width: 95%;
        height: 30px;
    }

    .button-order {
        margin-left: 10px;
    }
}
header {
    padding: 30px;
    background-color: #f1f1f1;
    -webkit-box-shadow: 0 5px 5px #dedede;
            box-shadow: 0 5px 5px #dedede;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.logo {
    color: #ff9d00;
    font-weight: bold;
    font-size: 50px;
    font-family: 'Anton', sans-serif;
}

@media (max-width: 600px) {
    header {
        padding: 10px;
        -ms-flex-direction: column;
            flex-direction: column;
        /*align-items: flex-start;*/
    }

    .logo {
        font-size: 30px;
    }
}
.infinite-scroll-component {
    display: -ms-flexbox;
    display: flex;
    padding: 60px;
    padding-bottom: 120px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    /*align-items: flex-end;*/
    position: relative;
}

.infinite-scroll-component li {
    /*flex-grow: 1;*/
    margin-bottom: 50px;
    list-style: none;
}

.loadInfo {
    position: absolute;
    bottom: 100px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

@media (max-width: 400px) {
    .MovieList {
        padding: 20px;
        padding-bottom: 100px;
    }
}
.MovieCard {
    width: 250px;
    height: 350px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 40px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 10px;
    cursor: pointer;
    position: relative;
    -webkit-box-shadow: 0px 0px 5px #acacac;
            box-shadow: 0px 0px 5px #acacac;
    margin: 0 10px;
}

.MovieCard:hover {
    -webkit-box-shadow: 0px 0px 15px #868686;
            box-shadow: 0px 0px 15px #868686;
}

.movieRating {
    position: absolute;
    background-color: whitesmoke;
    border-radius: 10px;
    text-align: center;
    padding: 15px;
    right: 20px;
    bottom: 25%;
    font-weight: bold;
    /*box-shadow: 1px 1px 1px #aaaaaa;*/
}

.cardFooter {
    position: absolute;
    bottom: 0;
    height: 20%;
    width: 100%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 40px;
    padding: 15px;
    padding-right: 20px;
    background-color: rgba(126, 126, 126, 0.7);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    color: whitesmoke;
    font-weight: bold;
    z-index: 2;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.movieTitle {
    font-size: 14px;
}

.cardFooter:hover {
    height: 100%;
    background-color: rgba(126, 126, 126, 0.9);
    border-top-left-radius: 40px;
    border-top-right-radius: 10px;
    padding: 20px;
}
.cardFooter:hover>.movieDescription {
    height: auto;
}

.movieDescription {
    max-height: 70%;
    overflow-y: hidden;
    height: 0;
    font-size: 11px;
    font-weight: normal;
}


@media (max-width: 600px) {
    .MovieCard {
        width: 200px;
        height: 300px;
    }

    .movieRating {
        padding: 10px;
        right: 10px;
    }

    .movieTitle {
        font-size: 14px;
    }

    .cardFooter {
        font-size: 14px;
        padding: 8px;
    }
}

