.search-wrapper {
    flex-grow: 1;
    max-height: 95px;
    border-radius: 8px;
    background-color: rgba(253, 156, 1, 0.8);
    margin-left: 20px;
    display: flex;
    padding: 20px;
}

.search-elem {
    background-color: transparent;
    outline: none;
    border: none;
    border-radius: 8px;
}

.button-order {
    margin-left: 60px;
    font-size: 20px;
}

.button-sort {
    font-weight: bold;
    margin-left: 30px;
}

.button-search {
    background-color: transparent;
    padding: 0;
    padding-right: 8px;
}

.input-wrapper {
    background-color: whitesmoke;
    height: auto;
    display: flex;
}

.input-actors {
    padding: 0 10px;
}

button {
    padding: 0 8px;
    cursor: pointer;
    position: relative;
}

button::after {
    color: whitesmoke;
    content: 'Info';
    position: absolute;
    top: -17px;
    left: 50%;
    transform: translate(-50%, 0);
    font-size: 14px;
    font-weight: bold;
}

.button-sort::after {
    content: 'sort';
}
.button-order::after {
    content: 'order';
}
.button-search::after {
    content: '';
}

@media (max-width: 600px) {
    .search-wrapper {
        width: 100%;
        max-height: 100px;
        padding: 5px;
        margin-left: 0;
        flex-wrap: wrap;
    }

    .input-wrapper {
        width: 100%;
        margin-bottom: 10px;
    }

    .input-actors {
        width: 95%;
        height: 30px;
    }

    .button-order {
        margin-left: 10px;
    }
}