.infinite-scroll-component {
    display: flex;
    padding: 60px;
    padding-bottom: 120px;
    flex-wrap: wrap;
    justify-content: space-around;
    /*align-items: flex-end;*/
    position: relative;
}

.infinite-scroll-component li {
    /*flex-grow: 1;*/
    margin-bottom: 50px;
    list-style: none;
}

.loadInfo {
    position: absolute;
    bottom: 100px;
    left: 50%;
    transform: translateX(-50%);
}

@media (max-width: 400px) {
    .MovieList {
        padding: 20px;
        padding-bottom: 100px;
    }
}